<template>
  <van-nav-bar left-arrow fixed :title="pageTitle" @click-left="handleBack">
    <template #right>
      <img src="../assets/images/kf.png" />
      <!-- <img src="../assets/images/kf1.png" v-show="!fixed" /> -->
    </template>
  </van-nav-bar>

  <div class="game-content" ref="mainEl">
    <div v-for="(item, index) in gameList" :key="index">
      <div class="game-item" v-show="active === index">
        <!-- <img src="https://q.yb-visa.com/static/media/xyzxInner.5c4706f4.jpg" /> -->
        <img :src="require('../assets/images/' + item.topimage)" />
        <div class="content">
          <div class="box1">
            <div class="name">{{ item.name }}</div>
            <div class="">{{ item.power }}x倍率</div>
          </div>
          <div class="tran-limit">
            <div>转账限额:</div>
            <div>{{ item.usdt }} <span>USDT</span></div>
            <div>{{ item.trx }} <span>TRX</span></div>
          </div>
        </div>
      </div>
    </div>
    <div class="switch-cont">
      <div class="btn" :class="{ 'active': idx === active }" @click="active = idx" v-for="(it, idx) in gameList" :key="idx">
        {{ it.name }}
      </div>
    </div>
    <div class="swipe__indicators">
      <div class="swipe__indicator" :class="{ 'active': idx === active }" @click="active = idx" v-for="(it, idx) in gameList" :key="idx">
      </div>
    </div>
    <van-cell-group class="transparent">
      <van-popover v-model:show="showPopover" placement="bottom-start">
        <div class="address-cont">
          <div class="addr-item" v-for="(addr, index) in historyList" :key="index" @click="handleChooseAddr(addr)">
            {{ addr }}</div>
        </div>
        <template #reference>
          <van-field v-model="walletAddr" @click="handleModifyAddress" placeholder="输入地址查询开奖结果" autocomplete="off">
            <template #button>
              <van-button size="small" type="primary" @click.stop="handleSearch">一键查询</van-button>
            </template>
          </van-field>
        </template>
      </van-popover>

    </van-cell-group>
    <van-cell-group>
      <div class="subtitle">
        <img src="../assets/images/tzjl.png" />
        <div class="text">投注记录</div>
      </div>
      <div class="table-list">
        <van-row class="head-cont">
          <van-col span="6">币种</van-col>
          <van-col span="6">注数</van-col>
          <van-col span="6">下注金额</van-col>
          <van-col span="6">今日输赢</van-col>
        </van-row>
        <van-row v-for="item in recordTotal" :key="item">
          <van-col span="6" class="font-blue">{{ item.coinName }}</van-col>
          <van-col span="6" class="font-blue">{{ item.count }}</van-col>
          <van-col span="6" class="font-blue">{{ item.money }}</van-col>
          <van-col span="6" class="font-blue">{{ item.winNum }}</van-col>
        </van-row>
        <van-row v-for="item in 2 - recordTotal.length" :key="item">
          <van-col span="6">&nbsp;</van-col>
          <van-col span="6"></van-col>
          <van-col span="6"></van-col>
          <van-col span="6"></van-col>
        </van-row>
      </div>
      <div class="table-list large">
        <van-row class="head-cont">
          <van-col span="3">币种</van-col>
          <van-col span="4">投注金额</van-col>
          <van-col span="4">回款</van-col>
          <van-col span="3">哈希</van-col>
          <van-col span="6">下注时间</van-col>
          <van-col span="4">状态</van-col>
        </van-row>
        <van-swipe class="my-swipe" :autoplay="0">
          <!-- recordList -->
          <van-swipe-item v-for="(arr, index) in recordList" :key="index">
            <van-row v-for="(it, idx) in arr" :key="idx">
              <van-col span="3">{{ it.token }}</van-col>
              <van-col span="4">{{ it.betNum }}</van-col>
              <van-col span="4" class="font-blue">{{ it.resultNum }}</van-col>
              <van-col span="3">{{ it.hxAddr }}</van-col>
              <van-col span="6">{{ it.realTime }}</van-col>
              <van-col span="4">{{ it.status  }}</van-col>
            </van-row>
            <van-row v-for="i in 5 - arr.length" :key="i">
              <van-col span="4">&nbsp;</van-col>
              <van-col span="4"></van-col>
              <van-col span="4"></van-col>
              <van-col span="6"></van-col>
              <van-col span="6"></van-col>
            </van-row>
          </van-swipe-item>
        </van-swipe>
      </div>
    </van-cell-group>
    <van-cell-group class="transparent" v-if="gameData">
      <instructions :name="gameData.name" :show-title="false" />
    </van-cell-group>
  </div>

  <div class="footer" v-if="gameData">
    <div class="buy-title">买【{{ gameData.name }}】投注地址</div>
    <div class="address">
      <span>{{ gameData.address }}</span>
      <van-icon name="qr" color="#4080ff" size="20" @click="handleQr(gameData)" />
    </div>
    <div class="copy-btn" @click="copy(gameData)">
      <img src="../assets/images/copy3.png" />
      一键复制
    </div>
    <warn @open="handleOpen" />
  </div>
  <div class="toast-box" v-if="gameItem">
    <div class="content">
      <span class="gukJud">投注地址复制成功！</span>
      <span class="gukJud">您投注的游戏为：【<span color="#FAFF00">{{ gameItem.name }}</span>】，投注限额为：</span>
      <span class="gukJud">{{ gameItem.trx }} USDT</span><span class="gukJud">{{ gameItem.usdt }} TRX</span>
    </div>
  </div>
  <van-popup v-model:show="showQrLayer" round>
    <div class="qr-layer">
      <div class="main-title">买【{{ qrData.name }}】投注地址二维码</div>
      <div class="qr-cont">
        <img :src="require('../assets/images/' + qrData.address + '.png')" />
        <p>扫描二维码转账投注</p>
      </div>
      <warn @open="handleOpen" />
    </div>
  </van-popup>
  <van-popup v-model:show="showLayer" closeable position="bottom">
    <register v-show="layerType === 'register'" @open="handleOpen" />
    <prohibit v-show="layerType === 'prohibit'" @open="handleOpen" />
  </van-popup>
  <van-popup round v-model:show="showLogin">
    <login-vue @login="handleLogin" />
  </van-popup>
</template>

<script>
import store from '../store';
import Warn from '../components/Warn';
import Register from "../components/Register";
import Prohibit from "../components/Prohibit"
import useClipboard from "vue-clipboard3";
import LoginVue from "../components/Login";
import { Subtr } from "../utils/public"
import Instructions from '../components/Instructions';
// import { ref, onMounted } from "vue"
import { formatTime } from "../utils/formatTime"
import { gameList } from "../utils/resources"

export default {
  name: "Recreation",
  components: { Warn, Register, Prohibit, LoginVue, Instructions },
  data() {
    return {
      pg: 1,
      ps: 0,
      active: 0,
      gameList: gameList,
      walletAddr: '',
      historyList: [],
      showRegister: false,
      showProhibit: false,
      timer: null,
      qrData: null,
      gameItem: null,
      showQrLayer: false,
      layerType: "",
      showLayer: false,
      showLogin: false, //登录弹窗
      recordTotal: [],//投注记录总的数据
      recordList: [[]],//投注记录数据
      showPopover: false
    }
  },
  created() {
    const { toClipboard } = useClipboard();
    this.toClipboard = toClipboard;
    let { id } = this.$route.query;
    if (id > 0) this.active = +id;
    this.getGameList();
    this.searchByAddr(this.walletAddr);
  },

  methods: {
    getGameList() {
      this.$toast.loading({ message: '加载中...', duration: 0, overlay: true })
      this.get("get_games").then(({ list }) => {
        this.$toast.clear();
        this.gameList.forEach(it => {
          list.find(({ name, address, trx, usdt ,power}) => {
            if (it.name === name) {
              it.address = address;
              it.trx = trx;
              it.usdt = usdt;
              it.power = power;
              return true;
            }
            return false;
          })
        })
        // let { mainEl } = this.$refs;
        // if (mainEl) mainEl.addEventListener("scroll", this.onscroll, true)
      })
    },
    handleBack() {
      this.$router.back();
    },
    onChange(index) {
      this.active = index;
    },
    handleSearch() {
      if (!this.walletAddr) return this.$toast("输入地址查询开奖结果!");
      this.showPopover = false;
      let { token } = store.getters;
      if (!token) return this.showLogin = true;
      this.$toast.loading({ message: '加载中...', duration: 0, overlay: true });
      this.post("get_user_all", {
        limit: 1, size: 40,
        screen: {
          address: this.walletAddr,
          value_token: ['usdt', 'trx'],
          value_inaddress: [this.gameData.address]
        }
      }, false).then(res => {
        this.$toast.clear();
        let rate = 1000000;
        let list = res.data || [];
        let datalist = [];
        let usdtNum = 0, trxNum = 0;
        let usdtBackNum = 0, trxBackNum = 0;
        let usdtCount = 0, trxCount = 0;
        list.forEach(it => {
          if (it.token == 'usdt') {
            usdtCount += 1;
            usdtNum += it.num;
            usdtBackNum = Subtr(usdtBackNum, -it.out_num);
          } else {
            trxCount += 1;
            trxNum += it.num;
            trxBackNum = Subtr(trxBackNum, -it.out_num);
          }
          it.resultNum = (it.out_num / rate).toFixed(2);
          it.betNum = (it.num / rate).toFixed(2);
          let time = new Date(it.time);
          time.setHours(time.getHours() + 8);
          it.realTime = formatTime(time, '', '-');
          let len = it.hash.length;
          it.hxAddr = it.hash.substring(len - 5, len);
          if (it.win === 1) it.status = "输";
          if (it.win === 0) it.status = "和";
          if (it.win === -1) it.status = "赢";
          if (it.win === -3) it.status = "金额过小";
          if (it.win === -4) it.status = "金额过大";
          if (it.win === -5) it.status = "出错";
        })
        for (let i = 0; i < list.length;) {
          datalist.push(list.slice(i, i + 5));
          i += 5;
        }
        this.recordTotal = [
          { coinName: 'USDT', count: usdtCount, money: (usdtNum / rate).toFixed(2), winNum: ((usdtBackNum - usdtNum) / rate).toFixed(2) },
          { coinName: 'TRX', count: trxCount, money: (trxNum / rate).toFixed(2), winNum: (Subtr(trxBackNum, trxNum) / rate).toFixed(2) },
        ]
        // list.forEach(it => {
        //   datalist.push(it);
        // });
        this.ps = Math.ceil(res.total / 5);
        this.recordList = datalist;
      })
    },
    searchByAddr(addr) {
      if (!addr) return
      let list = store.getters.history_address || [];
      let index = list.findIndex(it => it === addr)
      if (index >= 0) {
        list.splice(index, 1);
      }
      list.unshift(addr);
      this.historyList = list;
      console.log(list)
      store.commit("updateHistoryAddress", list);
    },
    handleChooseAddr(addr) {
      this.walletAddr = addr;
      this.showPopover = false;
    },
    handleOpen(type) {
      this.showQrLayer = false;
      this.layerType = type === 1 ? 'register' : 'prohibit'
      this.showLayer = true;
    },
    copy(item) {
      this.toClipboard(item.address);
      if (this.timer) clearTimeout(this.timer);
      this.gameItem = null;
      this.$nextTick(() => {
        this.gameItem = item;
      })
      this.timer = setTimeout(() => {
        this.gameItem = null;
      }, 1500)
    },
    //点击二维码
    handleQr(item) {
      this.qrData = item;
      this.showQrLayer = true;
    },
    //点击登录
    handleLogin() {
      this.showLogin = false;
    },
    handleModifyAddress() {
      this.walletAddr = "";
      if (this.historyList.length > 0) {
        this.historyLayer = true;
      }
    },
  },
  computed: {
    gameData() {
      if (!this.gameList.length) return ""
      return this.gameList[this.active];
    },
    pageTitle() {
      if (!this.gameList.length) return ""
      return this.gameList[this.active].name;
    }
  }
}
</script>


<style lang="scss" scoped>
.van-nav-bar.transparent {
  background-color: transparent;

  :deep(.van-nav-bar__left) {
    .van-icon {
      // color: #fff;
    }
  }

  :deep(.van-nav-bar__title) {
    // color: #fff;
  }

  &:after {
    height: 0;
  }
}

.game-content {
  min-height: 100%;
  padding-bottom: 250px;
  background-color: #f3f7ff;

  .game-item {
    position: relative;
    border-radius: 0 0 16px 16px;
    overflow: hidden;

    > img {
      width: 100%;
    }

    .content {
      position: absolute;
      left: 0;
      right: 0;
      bottom: 0;
      height: 50%;
      display: flex;
      color: #0d88fb;
      font-weight: bold;
      align-items: flex-start;

      .box1 {
        flex: 1;
        font-size: 14px;
        text-align: center;

        .name {
          font-size: 20px;
          margin-bottom: 4px;
        }
      }

      .tran-limit {
        flex: 2;

        div {
          margin-top: 4px;

          &:first-child {
            margin-top: 0;
          }
        }
      }
    }
  }

  .switch-cont {
    display: flex;
    padding: 10px;
    align-items: center;
    justify-content: center;

    .btn {
      padding: 0 6px;
      line-height: 40px;
      margin-right: 10px;
      border-radius: 10px;
      background-color: #fff;

      &.active {
        color: #fff;
        background-color: var(--main-color);
      }

      &:last-child {
        margin-right: 0;
      }
    }
  }

  .swipe__indicators {
    display: flex;
    align-items: center;
    justify-content: center;

    .swipe__indicator {
      width: 16px;
      height: 6px;
      margin-right: 10px;
      border-radius: 3px;
      background-color: #9fb8ee;

      &:last-child {
        margin-right: 0;
      }
    }

    .active {
      background-color: var(--main-color);
    }
  }

  .subtitle {
    position: relative;
    margin: 10px 0;

    img {
      width: 100%;
    }

    .text {
      color: #333;
      position: absolute;
      left: 0;
      right: 0;
      bottom: 0;
      display: flex;
      align-items: center;
      font-size: 16px;
      font-weight: bold;
    }
  }

  .van-cell-group {
    padding: 10px;
    margin: 0 10px 10px;

    .van-field {
      height: 40px;
      padding: 5px 10px;
      border-radius: 8px;

      :deep(.van-field__control) {
        font-size: 12px;
      }

      :deep(.van-field__button) {
        .van-button {
          color: var(--main-color);
          border: none;
          background-color: #f3f7ff;
        }
      }
    }

    &.transparent {
      background-color: transparent;
    }
  }

  .table-list {
    margin-bottom: 10px;

    .van-row {
      height: 20px;
      text-align: center;
      align-items: center;

      .font-blue {
        color: var(--main-color);
        font-weight: bold;
      }

      &:first-child {
        border-top-left-radius: 6px;
        border-top-right-radius: 6px;
      }

      &:last-child {
        border-bottom-left-radius: 6px;
        border-bottom-right-radius: 6px;
      }

      &:nth-child(even) {
        background-color: #ecf3ff;
      }

      &:nth-child(odd) {
        background-color: #e3edff;
      }

      &.head-cont {
        background-color: #d7e6ff;
      }
    }

    &.large {
      .van-row {
        height: 36px;
      }
    }
  }

  .my-swipe {
    .van-swipe-item {
      .van-row {
        &:first-child {
          border-radius: 0;
        }
      }
    }

    :deep(.van-swipe__indicators) {
      position: static;
      padding-top: 10px;
      transform: translate(0);
      justify-content: center;

      .van-swipe__indicator {
        background-color: var(--main-color);
      }
    }
  }
}

:deep(.van-popover__wrapper) {
  width: 100%;
}

.van-popover {
  .van-popover__content {
    .address-cont {
      padding: 5px 0;

      .addr-item {
        padding: 10px 10px;
        font-size: 12px;
        border-bottom: 1px solid #f1f1f1;

        &:last-child {
          border-bottom: 0;
        }
      }
    }
  }
}

.footer {
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 900;
  color: #fff;
  border-radius: 10px 10px 0 0;
  text-align: center;
  padding: 20px 10px;
  background-color: var(--main-color);

  .buy-title {
    padding-bottom: 5px;
    font-size: 18px;
    font-weight: bold;
    color: #faff00;
  }

  .address {
    height: 40px;
    background: #ffffff;
    border-radius: 20px;
    margin: 5px auto;
    display: flex;
    align-items: center;
    justify-content: center;

    span {
      margin-right: 10px;
      font-size: 12px;
      font-weight: bold;
      color: var(--main-color);
    }
  }

  .copy-btn {
    height: 40px;
    color: var(--main-color);
    background: #ffffff;
    border-radius: 20px;
    display: flex;
    width: 120px;
    margin: 10px auto;
    align-items: center;
    justify-content: center;

    img {
      width: 16px;
      margin-right: 5px;
    }
  }

  :deep(.alarm) {
    text-shadow: -2px 0 4px #fff;

    span {
      color: #fff;
    }
  }
}

.toast-box {
  position: fixed;
  top: 50%;
  left: 50%;
  width: 90%;
  height: 120px;
  z-index: 3000;

  transform: translate(-50%, -50%);

  .content {
    display: flex;
    height: 100%;
    border-radius: 8px;
    align-items: center;
    color: #fff;
    font-size: 14px;
    justify-content: center;
    flex-direction: column;
    animation: fadeInUp 1s ease;
    background: rgba($color: #000000, $alpha: 0.8);

    .gukJud {
      padding: 2px 0;

      span {
        color: #faff00;
      }
    }
  }
}

.qr-layer {
  width: 300px;
  padding: 10px 0;
  text-align: center;

  .main-title {
    color: var(--main-color);
    font-size: 18px;
    font-weight: bold;
    text-align: center;
    margin-bottom: 10px;
  }

  .qr-cont {
    > img {
      max-width: 200px;
    }

    p {
      padding-top: 10px;
      font-size: 13px;
    }
  }
}
</style>